body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

nav {
  height: 70px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1)
}

nav > img {
  padding-top: 15px;
  padding-left: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
}

.hero {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.hero--header {
  margin-bottom: 16px;
}

.hero--text {
  margin-top: 0;
}

hero--image {
  max-width: 550px;
}

.card {
  width: 175px;
  font-size: 12px;
}

.card--image {
  width: 100%;
  border-radius: 9px;
}

.card--stats {
  display: flex;
  align-items: center;
}

.card--star {
  height: 14px;
}